<template>

	<a-card :bordered="false" class="header-solid h-full" :bodyStyle="{padding: 0,}">
		<template #title>
			<a-row type="flex" align="middle">
				<a-col :span="24" :md="12">
					<h5 class="font-semibold m-0">Blockchains Table</h5>
				</a-col>
			</a-row>
		</template>

		<a-table :columns="columns" :data-source="data" :loading="loading" :pagination="{'show-size-changer': true}">

			<div
					slot="filterDropdown"
					slot-scope="{ setSelectedKeys, selectedKeys, confirm, clearFilters, column }"
					style="padding: 18px"
			>
				<a-input
						v-ant-ref="c => (searchInput = c)"
						:placeholder="`Search ${column.dataIndex}`"
						:value="selectedKeys[0]"
						style="width: 188px; margin-bottom: 8px; display: block;"
						@change="e => setSelectedKeys(e.target.value ? [e.target.value] : [])"
						@pressEnter="() => handleSearch(selectedKeys, confirm, column.dataIndex)"
				/>
				<a-button
						type="primary"
						icon="search"
						size="small"
						style="width: 90px; margin-right: 8px"
						@click="() => handleSearch(selectedKeys, confirm, column.dataIndex)"
				>
					Search
				</a-button>
				<a-button size="small" style="width: 90px" @click="() => handleReset(clearFilters)">
					Reset
				</a-button>
			</div>
			<a-icon
					slot="filterIcon"
					slot-scope="filtered"
					type="search"
					:style="{ color: filtered ? '#108ee9' : undefined }"
			/>

			<template slot="chain" slot-scope="chain">
				<h6 style="text-transform: capitalize" class="m-0">
					{{ chain }}
				</h6>
			</template>

			<template slot="editBtn" slot-scope="row">
				<a-button @click="editBlockchain(row)" v-b-modal.blockchainTableModal type="link" class="btn-edit">
					Edit
				</a-button>
			</template>

		</a-table>

		<div @click="addNewBlockchain" v-b-modal.blockchainTableModal class="table-upload-btn">
			<a-button type="default" block>
				<svg width="16" height="16" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
					<path fill-rule="evenodd" clip-rule="evenodd" d="M3 17C3 16.4477 3.44772 16 4 16H16C16.5523 16 17 16.4477 17 17C17 17.5523 16.5523 18 16 18H4C3.44772 18 3 17.5523 3 17ZM6.29289 6.70711C5.90237 6.31658 5.90237 5.68342 6.29289 5.29289L9.29289 2.29289C9.48043 2.10536 9.73478 2 10 2C10.2652 2 10.5196 2.10536 10.7071 2.29289L13.7071 5.29289C14.0976 5.68342 14.0976 6.31658 13.7071 6.70711C13.3166 7.09763 12.6834 7.09763 12.2929 6.70711L11 5.41421L11 13C11 13.5523 10.5523 14 10 14C9.44771 14 9 13.5523 9 13L9 5.41421L7.70711 6.70711C7.31658 7.09763 6.68342 7.09763 6.29289 6.70711Z" fill="#111827"/>
				</svg>
				Add New Blockchain
			</a-button>
		</div>
	</a-card>

</template>

<script>
	export default ({
		name: 'BlockchainsTable',
		props: {
			data: {
				type: Array,
				default: () => [],
			},
			columns: {
				type: Array,
				default: () => [],
			},
			loading: {
				type: Boolean,
				default: false,
			},
		},
		data() {
			return {
				searchText: '',
				searchInput: null,
				searchedColumn: '',
			};
		},
		methods: {
			handleSearch(selectedKeys, confirm, dataIndex) {
				confirm();
				this.searchText = selectedKeys[0];
				this.searchedColumn = dataIndex;
			},
			handleReset(clearFilters) {
				clearFilters();
				this.searchText = '';
			},
			editBlockchain(value) {
				this.$emit('editBlockchain', value)
			},
			addNewBlockchain() {
				this.$emit('addNewBlockchain')
			}
		}
	})

</script>
